import React, { useState } from 'react';
import { motion } from 'framer-motion';

const DecryptModal = ({ onSubmit, onCancel }) => {
  const [decryptionPassword, setDecryptionPassword] = useState('');

  const handleSubmit = () => {
    onSubmit(decryptionPassword);
    setDecryptionPassword('');
  };

  return (
    <motion.div
      className="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
    >
      <motion.div
        className="bg-gray-800 rounded-lg p-6 shadow-lg max-w-sm w-full"
        initial={{ scale: 0.9 }}
        animate={{ scale: 1 }}
        exit={{ scale: 0.9 }}
        transition={{ duration: 0.3 }}
      >
        <h2 className="text-xl font-bold mb-4 text-white">Decrypt Note</h2>
        <input
          type="password"
          placeholder="Enter decryption password"
          value={decryptionPassword}
          onChange={(e) => setDecryptionPassword(e.target.value)}
          className="w-full px-4 py-2 mb-4 border border-gray-600 rounded-md text-black focus:outline-none focus:ring-2 focus:ring-green-400"
        />
        <div className="flex justify-end gap-2">
          <button
            onClick={onCancel}
            className="bg-gray-600 text-white px-4 py-2 rounded hover:bg-gray-500 transition"
          >
            Cancel
          </button>
          <button
            onClick={handleSubmit}
            className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition"
          >
            Decrypt
          </button>
        </div>
      </motion.div>
    </motion.div>
  );
};

export default DecryptModal;
