import React, { useState, useEffect } from 'react';
import CryptoJS from 'crypto-js';
import { motion, AnimatePresence } from 'framer-motion';
import DecryptModal from './DecryptModal';
import { Eye, EyeOff, Trash2, Sun, Moon } from 'lucide-react';

const App = () => {
  const hardcodedPassword = 'password';
  const [password, setPassword] = useState('');
  const [loggedIn, setLoggedIn] = useState(false);
  const [title, setTitle] = useState('');
  const [note, setNote] = useState('');
  const [notes, setNotes] = useState([]);
  const [decryptedNote, setDecryptedNote] = useState(null);
  const [decryptError, setDecryptError] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [modalNoteObj, setModalNoteObj] = useState(null);
  const [expandedNoteIndex, setExpandedNoteIndex] = useState(null);
  const [theme, setTheme] = useState('dark');

  useEffect(() => {
    const savedNotes = JSON.parse(localStorage.getItem('notes')) || [];
    setNotes(savedNotes);

    const savedTheme = localStorage.getItem('theme') || 'dark';
    setTheme(savedTheme);
    document.documentElement.classList.toggle('dark', savedTheme === 'dark');
  }, []);

  const toggleTheme = () => {
    const newTheme = theme === 'dark' ? 'light' : 'dark';
    setTheme(newTheme);
    document.documentElement.classList.toggle('dark', newTheme === 'dark');
    localStorage.setItem('theme', newTheme);
  };

  const handleLogin = () => {
    if (password === hardcodedPassword) {
      setLoggedIn(true);
    } else {
      alert('Invalid password!');
    }
  };

  const handleAddNote = () => {
    if (!title.trim() || !note.trim()) return;
    const encryptedNote = CryptoJS.AES.encrypt(note, hardcodedPassword).toString();
    const newNotes = [...notes, { title, encryptedNote }];
    setNotes(newNotes);
    setTitle('');
    setNote('');
    localStorage.setItem('notes', JSON.stringify(newNotes));
  };

  const openDecryptModal = (noteObj) => {
    setModalNoteObj(noteObj);
    setShowModal(true);
  };

  const handleModalDecrypt = (userPassword) => {
    if (modalNoteObj) {
      try {
        const bytes = CryptoJS.AES.decrypt(modalNoteObj.encryptedNote, userPassword);
        const originalNote = bytes.toString(CryptoJS.enc.Utf8);
        if (originalNote) {
          setDecryptedNote({ title: modalNoteObj.title, content: originalNote });
          setDecryptError('');
        } else {
          setDecryptedNote(null);
          setDecryptError('Incorrect password!');
        }
      } catch (error) {
        setDecryptedNote(null);
        setDecryptError('Error decrypting note');
      }
    }
    setShowModal(false);
  };

  const handleDeleteAllNotes = () => {
    setNotes([]);
    localStorage.removeItem('notes');
    setDecryptedNote(null);
    setDecryptError('');
    setExpandedNoteIndex(null);
  };

  const handleCloseDecryptedNote = () => {
    setDecryptedNote(null);
    setDecryptError('');
  };

  const toggleNoteExpansion = (index) => {
    setExpandedNoteIndex((prev) => (prev === index ? null : index));
  };

  return (
    <div className="min-h-screen bg-white dark:bg-gradient-to-br dark:from-gray-900 dark:to-black text-black dark:text-white pt-24 font-sans flex flex-col justify-between transition-colors duration-500">
      {/* Header */}
      <div className="fixed top-0 left-0 w-full bg-gray-100 dark:bg-gray-800 shadow-md z-50 px-6 py-4 transition-colors duration-500">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <h1 className="text-2xl md:text-3xl font-extrabold text-green-500 tracking-wide">SecureNoteX</h1>
          <div className="flex items-center gap-4">
            <p className="text-sm md:text-base text-gray-700 dark:text-gray-300">
              Your private notes. Encrypted, always.
            </p>
            <button
              onClick={toggleTheme}
              className="bg-gray-200 dark:bg-gray-700 text-black dark:text-white p-2 rounded-full transition"
              aria-label="Toggle Theme"
            >
              {theme === 'dark' ? <Sun size={20} /> : <Moon size={20} />}
            </button>
          </div>
        </div>
      </div>

      {/* Login */}
      {!loggedIn ? (
        <div className="flex justify-center items-center h-[calc(100vh-96px)]">
          <div className="bg-gray-100 dark:bg-gray-800 border border-green-500 p-10 rounded-2xl shadow-2xl w-96">
            <h2 className="text-3xl font-bold text-center mb-6 text-green-400">Login</h2>
            <input
              type="password"
              placeholder="Enter your password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-4 py-2 mb-4 text-black rounded-lg focus:outline-none focus:ring-2 focus:ring-green-400"
            />
            <button
              onClick={handleLogin}
              className="w-full bg-green-500 text-white py-2 rounded-lg hover:bg-green-600 transition duration-200"
            >
              Login
            </button>
          </div>
        </div>
      ) : (
        <div className="max-w-7xl mx-auto px-4 py-10 grid grid-cols-1 md:grid-cols-2 gap-8 items-start">
          {/* Create Note */}
          <div className="bg-gray-100 dark:bg-gray-800 border-l-4 border-green-500 p-6 rounded-2xl shadow-xl h-fit transition-colors">
            <h2 className="text-2xl font-bold mb-4 text-green-400">Create a Note</h2>
            <input
              type="text"
              placeholder="Note Title"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              className="w-full mb-4 px-4 py-2 text-black rounded-md focus:outline-none focus:ring-2 focus:ring-green-400"
            />
            <textarea
              placeholder="Enter your note content"
              value={note}
              onChange={(e) => setNote(e.target.value)}
              className="w-full h-32 mb-4 px-4 py-2 text-black rounded-md focus:outline-none focus:ring-2 focus:ring-green-400"
            />
            <button
              onClick={handleAddNote}
              className="w-full bg-green-500 text-white py-2 rounded-md hover:bg-green-600 transition"
            >
              Add Note
            </button>
          </div>

          {/* Saved Notes */}
          <div className="bg-gray-100 dark:bg-gray-800 border-l-4 border-green-500 p-6 rounded-2xl shadow-xl h-fit transition-colors">
            <h3 className="text-2xl font-bold mb-4 text-green-400">Saved Notes</h3>
            {notes.length > 0 ? (
              <ul className="space-y-4">
                {notes.map((noteObj, index) => (
                  <li
                    key={index}
                    className="bg-white dark:bg-gray-700 text-black dark:text-white shadow p-4 rounded-lg border-l-4 border-green-400 cursor-pointer"
                    onClick={() => toggleNoteExpansion(index)}
                  >
                    <div className="flex justify-between items-center">
                      <span className="font-semibold text-sm md:text-base">{noteObj.title}</span>
                      <span className="text-gray-500 text-xs flex items-center gap-1">
                        {expandedNoteIndex === index ? <EyeOff size={16} /> : <Eye size={16} />}
                      </span>
                    </div>

                    {expandedNoteIndex === index && (
                      <div className="mt-3 text-sm text-gray-700 dark:text-gray-200">
                        <p className="text-xs break-all mt-1">{noteObj.encryptedNote}</p>
                        <button
                          onClick={(e) => {
                            e.stopPropagation();
                            openDecryptModal(noteObj);
                          }}
                          className="mt-2 bg-blue-500 text-white px-3 py-1 rounded hover:bg-blue-600 transition text-sm"
                        >
                          Decrypt
                        </button>
                      </div>
                    )}
                  </li>
                ))}
              </ul>
            ) : (
              <p className="text-gray-600 dark:text-gray-300">No saved notes yet.</p>
            )}
            {notes.length > 0 && (
              <button
                onClick={handleDeleteAllNotes}
                className="mt-6 bg-red-500 text-white w-full py-2 rounded-md hover:bg-red-600 transition flex items-center justify-center gap-2"
              >
                <Trash2 size={16} />
                Delete All Notes
              </button>
            )}
          </div>
        </div>
      )}

      {/* Decrypted Note Display */}
      <AnimatePresence>
        {decryptedNote && (
          <motion.div
            key="decrypted-note"
            initial={{ opacity: 0, y: 20, scale: 0.95 }}
            animate={{ opacity: 1, y: 0, scale: 1 }}
            exit={{ opacity: 0, y: 20, scale: 0.95 }}
            transition={{ duration: 0.5 }}
            className="max-w-2xl mx-auto mt-8 bg-white/90 dark:bg-gray-800 text-gray-900 dark:text-white p-6 pt-10 rounded-xl shadow-2xl relative border border-green-500"
          >
            <h4 className="text-xl font-bold mb-2">Decrypted message: {decryptedNote.title}</h4>
            <p className="whitespace-pre-line">{decryptedNote.content}</p>
            <button
              onClick={handleCloseDecryptedNote}
              className="absolute top-2 right-2 text-sm bg-red-500 text-white px-2 py-1 rounded hover:bg-red-600 transition"
            >
              ×
            </button>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Decryption error */}
      {decryptError && (
        <div className="max-w-2xl mx-auto mt-4 text-red-400 text-center font-semibold">
          {decryptError}
        </div>
      )}

      {/* Modal */}
      <AnimatePresence>
        {showModal && (
          <DecryptModal
            onSubmit={handleModalDecrypt}
            onCancel={() => setShowModal(false)}
          />
        )}
      </AnimatePresence>

      {/* Footer */}
      <footer className="w-full bg-gray-100 dark:bg-gray-800 text-black dark:text-white py-6 mt-8 text-center border-t border-green-500 transition-colors">
        <p className="text-lg">© 2025 SecureNoteX - Keeping your notes safe, always.</p>
        <p className="text-sm text-gray-500 dark:text-gray-400">"Because your privacy matters!"</p>
      </footer>
    </div>
  );
};

export default App;
